import React from "react";
import { Form, Input, Button, message, Row, Col } from "antd";
import axios from "axios";

const ContactForm = () => {
    const [form] = Form.useForm();

    const handleSubmit = async (values) => {
        try {
            await axios.post("https://getform.io/f/b9b673a0-3dd5-4469-b3b0-bbb374d23cfc", values);
            message.success("Message sent successfully!");
            form.resetFields();
        } catch (error) {
            console.log(error);
            message.error("An error occurred while sending the message.");
        }
    };

    return (
        <Row justify="center" style={{ padding: '0 50px' }}>
            <Col span={24} md={12} lg={8}>
                <Form layout="vertical" form={form} onFinish={handleSubmit} className="text-center">
                    <Form.Item
                        name="name"
                        label="Name"
                        rules={[{ required: true, message: "Please enter a name" }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name="email"
                        label="Email"
                        rules={[
                            { required: true, message: "Please enter your email" },
                            { type: "email", message: "Please enter a valid email" },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        name="message"
                        label="Message"
                        rules={[{ required: true, message: "Please enter a message" }]}
                    >
                        <Input.TextArea />
                    </Form.Item>
                    <Form.Item style={{ textAlign: "center" }}>
                        <Button type="primary" htmlType="submit" style={{ backgroundColor: "#cf2e2e" }}>
                            SEND
                        </Button>
                    </Form.Item>
                </Form>
            </Col>
        </Row>
    );
};

export default ContactForm;
